  updateProfileImage() {
    

    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
//          this.f1 	= image.toString();
            this.f1 = image.toString()
            this.demoImg.nativeElement.src=this.f1
            this.analyze(this.f1, true);
          

     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }
  }

  
  selectImage() : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
      this.f1 = image.toString()
      this.demoImg.nativeElement.src=this.f1
      this.analyze(this.f1, true);    



     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
     this.fileInput.nativeElement.click();
   }   
  }
  

  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
            this.f1 = readerEvent.target.result;
            this.demoImg.nativeElement.src=this.f1
            this.analyze(this.f1, true);
          };
      
      reader.readAsDataURL(event.target.files[0]);
    }



  }
  logs: string[] = [];
  watch : any;
  
  
  getCurrentLocation() {

    let loading = this.loadingCtrl.create({
      content:'Locating...'
    });

    loading.present();

    let options = {timeout : 10000 , enableHighAccuracy:true};
    let locationObs = new Observable(observable => {
      this.geolocation.getCurrentPosition(options)
      .then(resp => {
        let lat = resp.coords.latitude;
        let lng = resp.coords.longitude;
        console.log('lat '+ lat +' == '+ 'long '+lng )
        this.logs.push(lat + ' ' + lng);
     //   let location = new google.maps.LatLng(lat, lng);
        console.log('current location '+location)
        observable.next(location);

        loading.dismiss();
        this.events.publish('mapa:obtener', '1');
      // },
      // (err) => {
      //   console.log('Geolocation err: '+ err);
      //   loading.dismiss();
      // })
      })
    })
    return locationObs;
  }


  
